<template>
   <div>
      <div class="info-message flex item-center p-4 rounded-xl items-center" :class="{'hidden': haveData}">
         <i class="fas fa-exclamation-circle text-3xl px-4"></i>
         <span>No existe ninguna información en la base de datos. <br> Inserte los datos accediendo al formulario a traves del boton + en la esquina superior derecha.</span>
      </div>
      <div class="flex justify-end items-center" v-if="haveData">
         <button 
         class="inline-flex border border-transparent bg-red rounded-md font-semibold text-xs uppercase text-white px-4 py-2 mr-auto" 
         @click="deleteRows"
         ><i class="fa fa-trash mx-1"></i></button>
         <label for="filter">Filtro: </label>
         <input id="filter" 
         class="form-control border-b my-4 px-3 no-underline
         focus:outline-none" 
         v-model="filters.alias.value"/>
      </div>      
      <v-table 
         v-if="columns.length > 0"
         :data="rows"
         :filters="filters"
         :currentPage.sync="currentPage"
         :pageSize="25"
         @totalPagesChanged="totalPages = $event">
         <thead slot="head" class="uppercase text-white">
            <th ><input type="checkbox" v-model="all" @click="clearSelection"></th>
            <th v-for="(col, index) in columns" :key="index">{{ col }}</th>
            <th v-if="actions.show">actions</th>
         </thead>
         <tbody slot="body" slot-scope="{displayData}">            
            <tr v-for="(row, i) in displayData" :key="row.id" :class="{'bg-row': (i%2 > 0)}">
               <td><input type="checkbox" :value="row.id" :checked="all" @click="getitemToDelete(row.id, $event)"></td>
               <td v-for="(col, index) in columns" :key="index">   
                  <span class="text-green" v-if="(typeof row[col] == 'boolean' && row[col])"><i class="fas fa-check"></i></span>
                  <span class="text-red" v-if="(typeof row[col] == 'boolean' && !row[col])"><i class="fas fa-minus-circle"></i></span>
                  <span v-if="(typeof row[col] != 'boolean')">{{ row[col] }}</span>                  
               </td>
               <td v-if="actions.show">
                  <button type="button" class="ui small button" 
                  @click="edit(row)"
                  v-if="actions.edit">
                     <i class="fa fa-edit text-indigo-700 hover:opacity-50 m-2"></i>
                  </button>    
                  <button type="button" class="ui small button" 
                  @click="deleteRow(row.id)"
                  v-if="actions.delete">
                     <i class="fa fa-trash text-red-600 hover:opacity-50 m-2"></i>
                  </button>     
               </td>               
            </tr>
         </tbody>
      </v-table>

      <smart-pagination v-if="totalPages > 0"
        :currentPage.sync="currentPage"
        :totalPages="totalPages"
      />
   </div>
</template>
<script>
//https://tochoromero.github.io/vuejs-smart-table/the-basics/#table
import authService from "@/services/user/auth.service"

export default { 
   props: {
      rows: [],    
      actions: {
         default: {
            show: true,
            edit: true,
            delete: true
         }
      },
      deleteCols: []  
   },
   name: 'AdminTable',
   data() {
      return {
         info: `No existe ninguna información en la base de datos. <br> Inserte los datos accediendo al formulario a traves del boton + en la esquina superior derecha.`,
         isAdmin: false,
         columns: [],
         tableInfo: [],         
         filters: {
            alias: { value: '', keys: ['alias'] }
         },
         all: false,
         items_id: [],
         currentPage: 1,
         totalPages: 0
      }
   },   
   created() {
      this.isAdmin = authService.validateRole(['ROLE_Admin', 'ROLE_Comercial'])
      this.initTable()

      // upd actions controlls
      this.actions.show = (this.actions.show) ? this.actions.show : true
      this.actions.edit = (this.actions.edit) ? this.actions.edit : false
      this.actions.delete = (this.actions.delete) ? this.actions.delete : false
   },
   computed: {
      haveData() {         
         if (this.columns.length <= 0){
            this.totalPages = 0
            return false
         }
         return true
      }
   },  
   watch: {
      // columns(v) {
      //    haveData = (v.length > 0) ? false : true
      // },
      rows() {
         this.initTable()         
      }
   }, 
   methods: {
      initTable() {            
         this.columns = []           
         if (this.rows != undefined){
            if(this.rows.length > 0) {
               const cols = Object.keys(this.rows[0])
               if (cols[0] == 'id') cols.shift()
               // shift other cols            
               cols.map(c => {
                  if(this.deleteCols.findIndex(e => e == c) < 0){
                     this.columns.push(c)
                  }
               })
            }
         }
      },    
      edit(rowData) {
         this.$emit('editData', rowData)  
      },
      clearSelection() {
         if (!this.all) {
            this.rows.map(e => {
               this.items_id.push(e.id)
            })
         } else this.items_id = []
      },
      getitemToDelete(item_id, e) {
         if(e.target.checked) {
            if(this.items_id.findIndex(i => i == item_id) == -1) {
               this.items_id.push(item_id)
            }
         } else {
            const del = this.items_id
            this.items_id = []
            del.map(i => {
               if(i != item_id)
                  this.items_id.push(i)
            })
         }
      },      
      deleteRow(rowData) {
         this.items_id = [rowData]    
         this.deleteRows()         
      },
      deleteRows() {    
         if(confirm("Desea realmente eliminar estos elementos?")) 
            this.$emit('deleteData', this.items_id)
      },      
   }
}
</script>
